:root {
  --primary-color: #2c3e50;
  --secondary-color: #3498db;
  --accent-color: #e74c3c;
  --background-color: #f8f9fa;
  --text-color: #333;
  --light-gray: #ecf0f1;
  --white: #fff;
  --shadow: 0 2px 4px #0000001a;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  color: var(--text-color);
  background-color: var(--background-color);
  font-family: Roboto, sans-serif;
  line-height: 1.6;
}

.header {
  background-color: var(--white);
  box-shadow: var(--shadow);
  z-index: 1000;
  width: 100%;
  position: fixed;
  top: 0;
}

.nav-container {
  grid-template-columns: 1fr auto 1fr;
  align-items: center;
  gap: 2rem;
  max-width: 1200px;
  margin: 0 auto;
  padding: 1rem;
  display: grid;
}

.logo {
  text-align: center;
}

.logo h1 {
  color: var(--primary-color);
  margin: 0;
  font-size: 1.8rem;
  font-weight: 700;
}

.nav-links {
  justify-content: flex-start;
  gap: 2rem;
  display: flex;
}

.nav-actions {
  justify-content: flex-end;
  align-items: center;
  gap: 1.5rem;
  display: flex;
}

.nav-link {
  color: var(--text-color);
  font-weight: 500;
  text-decoration: none;
  transition: color .3s;
}

.nav-link:hover {
  color: var(--secondary-color);
}

.cart-icon {
  cursor: pointer;
  font-size: 1.2rem;
  position: relative;
}

.cart-count {
  background-color: var(--accent-color);
  color: var(--white);
  border-radius: 50%;
  padding: .2rem .5rem;
  font-size: .8rem;
  position: absolute;
  top: -8px;
  right: -8px;
}

.login-button {
  background-color: var(--secondary-color);
  color: var(--white);
  cursor: pointer;
  border-radius: 4px;
  padding: .5rem 1rem;
  transition: background-color .3s;
}

.login-button:hover {
  background-color: #2980b9;
}

.dropdown {
  display: inline-block;
  position: relative;
}

.dropdown-content {
  background-color: var(--white);
  min-width: 200px;
  box-shadow: var(--shadow);
  z-index: 1001;
  border-radius: 4px;
  padding: .5rem 0;
  display: none;
  position: absolute;
}

.dropdown:hover .dropdown-content {
  display: block;
}

.dropdown-content a {
  color: var(--text-color);
  padding: .8rem 1rem;
  text-decoration: none;
  transition: background-color .3s;
  display: block;
}

.dropdown-content a:hover {
  background-color: var(--light-gray);
  color: var(--secondary-color);
}

.hero-section {
  text-align: center;
  height: 600px;
  color: var(--white);
  background-image: linear-gradient(#00000080, #00000080), url("https://images.unsplash.com/photo-1584622781860-6d1a0d0c9c0a?ixlib=rb-1.2.1&auto=format&fit=crop&w=1920&q=80");
  background-position: center;
  background-size: cover;
  justify-content: center;
  align-items: center;
  margin-top: 80px;
  display: flex;
}

.hero-content {
  max-width: 800px;
  padding: 2rem;
}

.hero-content h1 {
  text-shadow: 2px 2px 4px #0000004d;
  margin-bottom: 1rem;
  font-size: 3.5rem;
}

.hero-content p {
  text-shadow: 1px 1px 2px #0000004d;
  margin-bottom: 2rem;
  font-size: 1.5rem;
}

.cta-button {
  background-color: var(--secondary-color);
  color: var(--white);
  border-radius: 4px;
  padding: 1rem 2rem;
  font-size: 1.2rem;
  text-decoration: none;
  transition: background-color .3s, transform .3s;
  display: inline-block;
}

.cta-button:hover {
  background-color: #2980b9;
  transform: translateY(-2px);
}

#main-content {
  max-width: 1200px;
  min-height: calc(100vh - 380px);
  margin-top: 0;
  margin-left: auto;
  margin-right: auto;
  padding: 2rem;
}

.product-grid {
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  gap: 2rem;
  padding: 2rem 0;
  display: grid;
}

.product-card {
  background-color: var(--white);
  box-shadow: var(--shadow);
  border-radius: 8px;
  transition: transform .3s;
  overflow: hidden;
}

.product-card:hover {
  transform: translateY(-5px);
}

.product-image {
  object-fit: cover;
  width: 100%;
  height: 200px;
}

.product-info {
  padding: 1rem;
}

.product-title {
  margin-bottom: .5rem;
  font-size: 1.1rem;
  font-weight: 500;
}

.product-price {
  color: var(--accent-color);
  margin: .5rem 0;
  font-size: 1.2rem;
  font-weight: 700;
}

.add-to-cart {
  background-color: var(--secondary-color);
  width: 100%;
  color: var(--white);
  cursor: pointer;
  border: none;
  border-radius: 4px;
  margin-top: 1rem;
  padding: .8rem;
  transition: background-color .3s;
}

.add-to-cart:hover {
  background-color: #2980b9;
}

.footer {
  background-color: var(--primary-color);
  color: var(--white);
  margin-top: 2rem;
  padding: 3rem 0 1rem;
}

.footer-content {
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 2rem;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 2rem;
  display: grid;
}

.footer-section h3 {
  margin-bottom: 1rem;
  font-size: 1.2rem;
}

.social-links {
  gap: 1rem;
  display: flex;
}

.social-link {
  color: var(--white);
  text-decoration: none;
  transition: color .3s;
}

.social-link:hover {
  color: var(--secondary-color);
}

.footer-bottom {
  text-align: center;
  border-top: 1px solid #ffffff1a;
  margin-top: 2rem;
  padding-top: 1rem;
}

.hidden {
  display: none;
}

@media (width <= 768px) {
  .nav-container {
    flex-direction: column;
    gap: 1rem;
  }

  .nav-links {
    flex-direction: column;
    align-items: center;
    gap: 1rem;
  }

  .nav-actions {
    justify-content: center;
    width: 100%;
  }

  .product-grid {
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  }
}

.auth-container {
  background-color: var(--white);
  max-width: 400px;
  box-shadow: var(--shadow);
  border-radius: 8px;
  margin: 2rem auto;
  padding: 2rem;
}

.auth-form {
  flex-direction: column;
  gap: 1rem;
  display: flex;
}

.form-group {
  flex-direction: column;
  gap: .5rem;
  display: flex;
}

.form-group input {
  border: 1px solid var(--light-gray);
  border-radius: 4px;
  padding: .8rem;
  font-size: 1rem;
}

.cart-container {
  background-color: var(--white);
  box-shadow: var(--shadow);
  border-radius: 8px;
  padding: 2rem;
}

.cart-item {
  border-bottom: 1px solid var(--light-gray);
  grid-template-columns: 100px 1fr auto;
  align-items: center;
  gap: 1rem;
  padding: 1rem 0;
  display: grid;
}

.cart-item img {
  object-fit: cover;
  border-radius: 4px;
  width: 100px;
  height: 100px;
}

.cart-item-info p {
  color: var(--text-color);
  margin: .5rem 0;
  font-size: 1.1rem;
}

.cart-total {
  text-align: right;
  color: var(--primary-color);
  margin-top: 2rem;
  font-size: 1.3rem;
  font-weight: 700;
}

.payment-form {
  background-color: var(--white);
  max-width: 600px;
  box-shadow: var(--shadow);
  border-radius: 8px;
  margin: 2rem auto;
  padding: 2rem;
}

.payment-form .form-group {
  margin-bottom: 1.5rem;
}

.payment-form label {
  margin-bottom: .5rem;
  font-weight: 500;
}

.payment-form input {
  border: 1px solid var(--light-gray);
  border-radius: 4px;
  width: 100%;
  padding: .8rem;
  font-size: 1rem;
}

.payment-form button {
  background-color: var(--secondary-color);
  width: 100%;
  color: var(--white);
  cursor: pointer;
  border: none;
  border-radius: 4px;
  padding: 1rem;
  font-size: 1.1rem;
  transition: background-color .3s;
}

.payment-form button:hover {
  background-color: #2980b9;
}

.order-summary p {
  color: var(--primary-color);
  margin: 1rem 0;
  font-size: 1.2rem;
  font-weight: 600;
}

.category-header {
  text-align: center;
  background-color: var(--white);
  box-shadow: var(--shadow);
  border-radius: 8px;
  margin-bottom: 2rem;
  padding: 1rem;
}

.category-header h2 {
  color: var(--primary-color);
  margin: 0;
  font-size: 2rem;
}
